<template>
	<div ref="genderChart" class="echart-box"></div>
</template>

<script>
	export default {
		name: 'Gender',
		data() {
			return {
				list: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.genderChart);
				if(!chart){
					chart = this.$echarts.init(this.$refs.genderChart, 'light');
					this.$once('hook:beforeDestroy',()=>{
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					title: {
						text: '男女买家对比',
						left: 'center'
					},
					tooltip: {
						trigger: 'item',
						formatter: "<b>{a}</b> <br/>{b} : {c} <i>({d}%)</i>"
					},
					legend: {
						top: 'bottom',
						left: 'center'
					},
					series: [{
						name: '性别',
						type: 'pie',
						radius: ['40%', '70%'],
						itemStyle: {
							borderRadius: 10
						},
						data: [{
								value: this.list[1],
								name: '男性'
							}, {
								value: this.list[2],
								name: '未知'
							}, {
								value: this.list[0],
								name: '女性'
							},
						]
					}]
				}
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'data/gender.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.list.push(resp.data[i][1])
				}
				this.drawChart();
			})
		}
	}
</script>

